type checkURLType = (
    location: {
        hash: string,
        key:string,
        pathname: string,
        search: string,
        state: string
    }
) => string
//     URL
const checkURL: checkURLType = (location) => {
    const patch = location.pathname //   URL  /profile
        .split( "" ) //     ['/', 'd', 'i', 'a', 'l', 'o', 'g', 's', '/', '2', '8', '8', '3', '1',]
    const tempPatch: Array<String> = [] //   
    for (let i: number = 1; i < patch.length; i++) { //    ,    '/'
        if (patch[i] === '/') {
            break //  ,   /
        }
        tempPatch.push( patch[i] ) //    
    }
    return tempPatch.join( "" )//  

}
export default checkURL