import {inferActionsTypes} from "../store/store-redux";
import {commentType, postType} from "../../common/commonTypes/commonTypes";

export const GET_ALL_POSTS = "myApp/app-reducer/GET_ALL_POSTS"; //   
export const SET_ALL_POSTS = "myApp/app-reducer/SET_ALL_POSTS"; //     
export const GET_COMMENTS_BY_POST_ID = "myApp/app-reducer/GET_COMMENTS_BY_POST_ID"; //    ID 
export const SET_COMMENTS_TO_STATE = "myApp/app-reducer/SET_COMMENTS_TO_STATE"; //      ID 
export const SET_PAGINATION_DATA = "myApp/app-reducer/SET_PAGINATION_DATA"; //     
export const SET_SHOW_COMMENTS = "myApp/app-reducer/SET_SHOW_COMMENTS"; //     
export const SET_SEARCH_POST_QUERY = "myApp/app-reducer/SET_SEARCH_POST_QUERY"; //     
export const SET_SORT_HEADER_DIRECTION = "myApp/app-reducer/SET_SORT_HEADER_DIRECTION"; //    

export const allPostsActions = {
    getAllPostsAC: () => { //     
        return {type: GET_ALL_POSTS} as const
    },
    setAllPostsAC: (allPosts: Array<postType>) => { //       
        return {type: SET_ALL_POSTS, allPosts} as const
    },
    getCommentsByPostIdAC: (postId: number) => { //      ID 
        return {type: GET_COMMENTS_BY_POST_ID, postId} as const
    },
    setCommentsByPostIdAC: (CommentsByPostId: Array<commentType>) => { //        ID 
        return {type: SET_COMMENTS_TO_STATE, CommentsByPostId} as const
    },
    setPaginationDataAC: (paginationData: paginationDataType) => { //        ID 
        return {type: SET_PAGINATION_DATA, paginationData} as const
    },
    setShowCommentsAC: (showComments: Array<number>) => { //       showComments,
        return {type: SET_SHOW_COMMENTS, showComments} as const
    },
    setSearchPostQueryAC: (searchPostQuery: string) => { //
        return {type: SET_SEARCH_POST_QUERY, searchPostQuery} as const
    },
    setSortHeaderDirectionAC: (sortHeaderDirection: boolean | undefined) => { //
        return {type: SET_SORT_HEADER_DIRECTION, sortHeaderDirection} as const
    }
}

type allPostsActionsTypes = inferActionsTypes<typeof allPostsActions>

export const PostsInitialState = {//  
    allPosts: [] as Array<postType>, //  
    allComments: [] as Array<commentType>,//   
    showComments: [] as Array<number>, //  ID ,     
    paginationData: {//   
        pageSize: 5, //     
        urrentPage: 1, //   
        currentRangeLocal: 1, //   
        portionSize: 5, //      
    },
    searchPostQuery: "", //  
    sortHeaderDirection: undefined as boolean | undefined// 
}
export type paginationDataType = typeof PostsInitialState.paginationData

export type allPostsInitialStateType = typeof PostsInitialState

const AllPostsReducer = (state: allPostsInitialStateType = PostsInitialState, action: allPostsActionsTypes): allPostsInitialStateType => {//  
    switch (action.type) {
        case SET_ALL_POSTS:  //    
            return {
                ...state, //   
                allPosts: action.allPosts, //      true
            }
        case SET_COMMENTS_TO_STATE:  //     
            const allCommentsFiltered: Array<commentType> = //        ID ,    
                state.allComments.filter( comment => comment.postId !== action.CommentsByPostId[0].postId )
            return {
                ...state, //   
                allComments: [...allCommentsFiltered, ...action.CommentsByPostId], //      ID    
            }
        case SET_PAGINATION_DATA:  //      
            return {
                ...state, //   
                paginationData: action.paginationData, //      
            }
        case SET_SHOW_COMMENTS:  //   showComments  
            return {
                ...state, //   
                showComments: action.showComments, //    showComments  
            }
        case SET_SEARCH_POST_QUERY:  //
            return {
                ...state, //   
                searchPostQuery: action.searchPostQuery, //   searchPostQuery  
            }
        case SET_SORT_HEADER_DIRECTION:  //    
            return {
                ...state, //   
                sortHeaderDirection: action.sortHeaderDirection, //   sortHeaderDirection  
            }
        default:
            return state; //     
    }
}

export default AllPostsReducer